// Project
#PROJECT {
  margin-top: 5rem;

  opacity: 0;
  transform: translateY(100px);
  transition: all 1s;

  .project_title {
    display: flex;
    align-items: center;
    gap: 0.5rem;

    img {
      width: 30px;
      height: 30px;

      transform: rotate(0deg);
      animation: titleIconAnimation 3s 1s ease-in-out infinite;
    }

    h2 {
      font-size: 2rem;
      font-weight: 600;
      font-family: var(--mainfont-playfairDisplay);
    }
  }

  .project_list {
    margin-top: 2rem;

    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);

    .project {
      padding: 2rem;
      background-color: var(--black400);
      border-radius: 1rem;

      .project_imgBox {
        padding-bottom: 2rem;

        display: flex;
        justify-content: center;

        border-bottom: 1px solid #ccc;

        img {
          width: 250px;
        }
      }

      .project_info {
        margin-top: 2rem;

        h3 {
          font-weight: bolder;
          color: var(--subColorSkyBlue);
        }

        p {
          margin: 1rem 0 0;
          height: 110px;
        }

        .project_skill {
          display: flex;
          align-items: center;
          gap: 1rem;
          color: var(--subColorSkyBlue);

          div {
            padding: 0 0.5rem;
            border: 1px solid var(--subColorSkyBlue);
            border-radius: 1rem;
          }
        }
      }

      .project_link {
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;

        a {
          padding: 0.5rem 0;
          color: var(--white);
          font-weight: bolder;
          text-align: center;
        }

        a:nth-of-type(1) {
          background-color: var(--subColorSkyBlue);
        }

        a:nth-of-type(2) {
          background-color: var(--black200);
        }
      }
    }
  }
}

#PROJECT.active {
  transform: translateY(0px);
  opacity: 1;
}

// 애니메이션
@keyframes titleIconAnimation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(45deg);
  }
  20% {
    transform: rotate(-45deg);
  }
  30% {
    transform: rotate(30deg);
  }
  40% {
    transform: rotate(-30deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(-10deg);
  }
  70% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

// Project 반응형
@media only screen and (max-width: 900px) {
  #PROJECT {
    .project_list {
      grid-template-columns: repeat(1, 1fr);
    }
  }
}

@media only screen and (max-width: 650px) {
  #PROJECT {
    .project_title {
      justify-content: center;
    }
  }
}

@media only screen and (max-width: 500px) {
  #PROJECT {
    .project_list {
      .project {
        .project_imgBox {
          img {
            width: 150px;
          }
        }
      }
    }
  }
}
